.slider {
    position:relative;
    width:100%;
}
.slider .pagination {
    display:none;
}
@media (min-width: $(desktop)) {
    .slider .pagination {
        display:block;
    }
}
.slides {
    width:100%;
    position:relative;


}
.slides .slide {
    left:100%;
    position:absolute;
    width:100%;
    -webkit-box-align: center;
    -webkit-align-items: center;
        -ms-flex-align: center;
                -ms-grid-row-align: center;
            align-items: center;
    -webkit-box-pack: center;
    -webkit-justify-content: center;
        -ms-flex-pack: center;
            justify-content: center;
    opacity:0;
}
.slides .slide:nth-child(1){
    left:0;
    opacity:1;
    position:relative;
}
@media (min-width: $(desktop)) {
    .slides .slide:nth-child(1) {
        position:absolute;
    }
}
